@import "../_variables.scss";

.main {

    width: 100vw;
    margin-top: 7rem;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;


    span {
        @include color-cycle;
    }

    .head {
        display: flex;
        flex-direction: row;
        justify-content: center;
        align-items: center;
        
    }

    h1 {
        font-size: 2.5rem;
        letter-spacing: 2px;
    }

    button {
        display: flex;
        justify-content: center;
        align-items: center;
        height: 10vh;
        width: 30vh;
        background-color: $acm-blue;
        color: white;
        margin-left: 4rem;
        border: none;
        border-radius: 2rem;
        padding: 1rem 1.5rem 1rem 1.5rem;
        font-size: 1.25rem;
        font-weight: 500;
        cursor: pointer;
        transition: background-color 0.2s, box-shadow 0.2s;
    }

    button:hover{
        background-color: darken-color($acm-blue);
        box-shadow: rgba(50, 50, 93, 0.25) 0px 2px 5px -1px, rgba(0, 0, 0, 0.3) 0px 1px 3px -1px;
        color: white;
    }


    .description{
        font-size: 1.5rem;
        padding: 0 33vw 0 33vw;
        letter-spacing: 1px;
    }

    .link-main {
        padding-top: 3rem;
        font-size: 2rem;
        text-decoration: none;
        
    }

    .posts-wrapper {
        margin-top: 1vh;
    }

}